@import url(https://fonts.googleapis.com/css2?family=Londrina+Solid:wght@300;400&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Barlow+Condensed:wght@200&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Quattrocento:wght@400;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Satisfy&display=swap);
* {
  margin: 0px;
  padding: 0px;
  box-sizing: border-box;
  /* border: 1px solid lime; */
}
body {
  display: flex;
  flex-direction: column;
}

/* Sarı #FDC913, Açık Gri: #5F5F5F, Koyu Gri #292929, Kırmızı #CE2829, Bej: #FAF7F2 */

/* font-family: 'Barlow', sans-serif;
font-family: 'Bebas Neue', sans-serif;
font-family: 'Merienda', cursive;
font-family: 'Roboto', sans-serif; */
/* font-family: 'Satisfy', cursive; */
/* Sarı #FDC913, Açık Gri: #5F5F5F, Koyu Gri #292929, Kırmızı #CE2829, Bej: #FAF7F2 */

/* hero section */
.homePage .hero {
  background-image: url(/static/media/mvp-banner.e1143cc0.png);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.homePage .hero header {
  display: flex;
  align-items: center;
  justify-content: center;
  max-width: 600px;
  height: 15%;
}

.homePage .hero header h1 {
  font-family: "Londrina Solid", sans-serif;
  font-weight: 300;
  font-size: 2.5rem;
  color: white;
}

.homePage .hero section {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  padding-top: 20px;
  max-width: 700px;
}

.homePage .hero section p:nth-child(1) {
  font-family: "Satisfy";
  color: #fdc913;
  font-size: 1.5rem;
}
.homePage .hero section p:nth-child(2) {
  width: 100%;
  font-size: 4rem;
  color: white;
  text-align: center;
  font-family: "Barlow Condensed";
  font-weight: 100;
}

.homePage .hero section button {
  height: 50px;
  width: 200px;
  font-weight: bold;
  border-radius: 100px;
  border: none;
  font-size: 1rem;
  background-color: #fdc913;
  -webkit-border-radius: 100px;
  -moz-border-radius: 100px;
  -ms-border-radius: 100px;
  -o-border-radius: 100px;
  margin-top: 20px;
}

.homePage .hero section button a {
  text-decoration: none;
}

/* navbar section */

.homePage .navbar {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 30px 0px;
}
.homePage .navbar ul {
  font-family: "Barlow", sans-serif;
  font-weight: bold;
  list-style-type: none;
  width: 70%;
  height: 50%;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  align-items: center;
  grid-gap: 10px;
  gap: 10px;
}
.homePage .navbar ul li {
  height: 50px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  grid-gap: 20px;
  gap: 20px;
}
.homePage .navbar ul li img {
  height: 100%;
}
.homePage .navbar ul li a {
  text-decoration: none;
  font-size: 1rem;
}

/* content section */
.homePage .content {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #faf7f2;
  padding-top: 100px;
  grid-gap: 100px;
  gap: 100px;
}

.homePage .content .featured {
  width: 70%;
  display: flex;
  align-items: center;
  justify-content: center;
  grid-gap: 20px;
  gap: 20px;
}

.homePage .content .featured button {
  font-family: "Barlow", sans-serif;
  width: 200px;
  height: 50px;
  background-color: white;
  color: #ce2829;
  font-weight: bold;
  border-radius: 100px;
  border: none;
  font-size: 1rem;
  -webkit-border-radius: 100px;
  -moz-border-radius: 100px;
  -ms-border-radius: 100px;
  -o-border-radius: 100px;
}
.homePage .content .featured .left-box {
  background-image: url(/static/media/kart-1.033ba8b0.png);
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  width: 50%;
  height: 50vh;
  border-radius: 10px;
  -webkit-border-radius: 10px;
  -moz-border-radius: 10px;
  -ms-border-radius: 10px;
  -o-border-radius: 10px;
  padding: 50px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  grid-gap: 20px;
  gap: 20px;
}
.homePage .content .featured .left-box h3 {
  font-family: "Quattrocento", serif;
  font-size: 4rem;
  color: white;
}
.homePage .content .featured .left-box p {
  font-size: 1.2rem;
  color: white;
  font-family: "Barlow", sans-serif;
}
.homePage .content .featured .right-box {
  width: 50%;
  height: 50vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  grid-gap: 20px;
  gap: 20px;
}
.homePage .content .featured .right-box .top {
  background-image: url(/static/media/kart-2.ad1212f4.png);
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  width: 100%;
  height: 50%;
  padding: 30px;
  border-radius: 10px;
  -webkit-border-radius: 10px;
  -moz-border-radius: 10px;
  -ms-border-radius: 10px;
  -o-border-radius: 10px;
  font-family: "Barlow", sans-serif;
}
.homePage .content .featured .right-box .top h4 {
  color: white;
  font-size: 2rem;
  margin-top: 20px;
  margin-bottom: 20px;
}
.homePage .content .featured .right-box .bottom {
  background-image: url(/static/media/kart-3.eba94db2.png);
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  width: 100%;
  height: 50%;
  padding: 30px;
  font-size: 2rem;
  border-radius: 10px;
  -webkit-border-radius: 10px;
  -moz-border-radius: 10px;
  -ms-border-radius: 10px;
  -o-border-radius: 10px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  grid-gap: 20px;
  gap: 20px;
  font-family: "Barlow", sans-serif;
}

.homePage .content .featured .right-box .bottom span {
  color: #ce2829;
}
/* menu section */
.homePage .content .menu {
  width: 70%;
  display: flex;
  flex-direction: column;
  align-items: center;
  grid-gap: 50px;
  gap: 50px;
  font-family: "Barlow", sans-serif;
  padding-bottom: 50px;
}
.homePage .content .menu .top-box {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  grid-gap: 30px;
  gap: 30px;
}
.homePage .content .menu .top-box p {
  height: 56px;
}
.homePage .content .menu .top-box p:nth-child(1) {
  font-family: "Satisfy";
  color: #ce2829;
  font-size: 1.5rem;
}
.homePage .content .menu .top-box p:nth-child(2) {
  font-size: 2.5rem;
  font-weight: 600;
  color: #292929;
}
.homePage .content .menu .top-box nav {
  width: 100%;
}
.homePage .content .menu .top-box nav ul {
  list-style-type: none;
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  grid-gap: 20px;
  gap: 20px;
}
.homePage .content .menu .top-box nav ul li {
  height: 80px;
  width: 200px;
  display: flex;
  justify-content: center;
  align-items: center;
  grid-gap: 10px;
  gap: 10px;
  border-radius: 40px;
  -webkit-border-radius: 40px;
  -moz-border-radius: 40px;
  -ms-border-radius: 40px;
  -o-border-radius: 40px;
  background-color: #ffffff;
  font-weight: bold;
}
.homePage .content .menu .top-box nav ul li:hover {
  background-color: #292929;
}
.homePage .content .menu .top-box nav ul li:hover > a {
  color: white;
}

.homePage .content .menu .top-box nav ul li img {
  height: 50%;
}
.homePage .content .menu .top-box nav ul li a {
  text-decoration: none;
  font-size: 1rem;
}

.homePage .content .menu .bottom-box {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
}

.homePage .content .menu .bottom-box .pizzaCard {
  width: 30%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: #ffffff;
  border-radius: 10px;
  -webkit-border-radius: 10px;
  -moz-border-radius: 10px;
  -ms-border-radius: 10px;
  -o-border-radius: 10px;
  transition: 0.3s;
  -webkit-transition: 0.3s;
  -moz-transition: 0.3s;
  -ms-transition: 0.3s;
  -o-transition: scale 0.3s;
}
.homePage .content .menu .bottom-box .pizzaCard:hover {
  scale: 1.05;
}
.homePage .content .menu .bottom-box .pizzaCard img {
  width: 80%;
}
.homePage .content .menu .bottom-box .pizzaCard p:nth-child(2) {
  font-size: 1.3rem;
  font-weight: bold;
  align-self: flex-start;
  padding-left: 30px;
}
.homePage .content .menu .bottom-box .pizzaCard p:nth-child(3) {
  width: 100%;
  padding: 20px;
  font-size: 1rem;
  display: flex;
  justify-content: space-between;
}

.homePage
  .content
  .menu
  .bottom-box
  .pizzaCard
  p:nth-child(3)
  span:nth-child(3) {
  font-weight: bold;
  font-size: 1.3rem;
}
@media (max-width: 1500px) {
  .homePage .content .featured {
    width: 90%;
  }
  .homePage .content .menu .bottom-box .pizzaCard p:nth-child(2) {
    font-size: 1.1rem;
  }
}

@media (max-width: 1200px) {
  .homePage .content .featured {
    width: 90%;
  }
  .homePage .content .menu .bottom-box .pizzaCard p:nth-child(2) {
    font-size: 1rem;
  }
  .homePage .content .menu .bottom-box .pizzaCard {
    font-size: 1rem;
    height: 300px;
  }
}

@media (max-width: 800px) {
  .homePage .content .featured {
    flex-direction: column;
    width: 80%;
  }
  .homePage .content {
    padding-top: 40px;
  }
  .homePage .content .featured .left-box {
    width: 100%;
  }
  .homePage .content .featured .right-box {
    width: 100%;
  }
  .homePage .content .featured button {
    width: 150px;
    height: 50px;
    border-radius: 25px;
    border: none;
    font-size: 1rem;
    -webkit-border-radius: 25px;
    -moz-border-radius: 25px;
    -ms-border-radius: 25px;
    -o-border-radius: 25px;
  }
  .homePage .content .menu {
    justify-content: center;
  }
  .homePage .content .menu .top-box p:nth-child(2) {
    font-size: 1.5rem;
  }

  .homePage .content .menu .top-box nav ul li {
    height: 50px;
    width: 150px;
    justify-content: space-around;
  }

  .homePage .content .menu .bottom-box {
    flex-direction: column;
  }
  .homePage .content .menu .bottom-box {
    grid-gap: 50px;
    gap: 50px;
  }
  .homePage .content .menu .bottom-box .pizzaCard {
    width: 100%;
    height: auto;
  }
  .homePage .content .menu .bottom-box .pizzaCard p:nth-child(2) {
    font-size: 1.5rem;
  }
}

@media (max-width: 600px) {
  .homePage .hero header h1 {
    font-size: 2rem;
  }
  .homePage .hero section p {
    font-size: 3rem;
  }
  .homePage .hero section {
    padding-top: 10px;
    justify-content: space-around;
  }
  .homePage .hero section button {
    height: 30px;
    width: 150px;
    border-radius: 50px;
    -webkit-border-radius: 50px;
    -moz-border-radius: 50px;
    -ms-border-radius: 50px;
    -o-border-radius: 50px;
  }
  .homePage .content .menu .bottom-box .pizzaCard {
  }
}

footer {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  background-color: #1a1a1a;
  color: white;
  padding-top: 50px;
}
footer div {
  display: flex;
}
footer section {
  display: flex;
}

footer .contact-info {
  width: 70%;
  padding: 20px 0px;
  justify-content: space-between;
  border-bottom: 1px solid white;
}

footer .contact-info .left-box section {
  flex-direction: column;
  grid-gap: 15px;
  gap: 15px;
  width: 300px;
}

footer .contact-info .left-box .address h2 {
  font-size: 2rem;
}
footer .contact-info .left-box .address p {
  display: flex;
  grid-gap: 10px;
  gap: 10px;
  font-size: 1rem;
  align-items: flex-end;
}
footer .contact-info .left-box .address p img {
  height: 100%;
}

footer .contact-info .left-box .hot-menu h4 {
  font-size: 1.3rem;
  margin-bottom: 30px;
}
footer .contact-info .left-box .hot-menu p {
  font-size: 1rem;
}

footer .contact-info .instagram {
  flex-direction: column;
  grid-gap: 20px;
  gap: 20px;
  width: 20vw;
  padding-left: 2vw;
}
footer .contact-info .instagram .fotos {
  width: 100%;
  flex-wrap: wrap;
  grid-gap: 10px;
  gap: 10px;
}

footer .contact-info .instagram img {
  height: 5vw;
}

footer .copyright {
  width: 70%;
  height: 50px;
  justify-content: space-between;
  align-items: center;
}
footer .copyright img {
  background-color: white;
  width: 20px;
  height: 20px;
}

@media (max-width: 1200px) {
  footer .contact-info {
    flex-direction: column;
    grid-gap: 20px;
    gap: 20px;
  }
  footer .contact-info .left-box {
    justify-content: space-around;
  }
  footer .contact-info .left-box section {
    width: 50%;
  }
  footer .contact-info .instagram {
    width: 100%;
  }
}

@media (max-width: 800px) {
  footer .contact-info .left-box {
    flex-direction: column;
    grid-gap: 20px;
    gap: 20px;

    width: 100%;
  }
  footer .contact-info .left-box section {
    width: 100%;
  }
  footer .contact-info .instagram {
    width: 100%;
  }
}

/* font-family: 'Barlow', sans-serif;
font-family: 'Bebas Neue', sans-serif;
font-family: 'Merienda', cursive;
font-family: 'Roboto', sans-serif; */
/* Sarı #FDC913, Açık Gri: #5F5F5F, Koyu Gri #292929, Kırmızı #CE2829, Bej: #FAF7F2 */

/* ************************************************** */
.orderPage {
  display: flex;
  flex-direction: column;
  align-items: center;
  font-family: "Barlow", sans-serif;
}
.orderPage #hero {
  background-color: #faf7f2;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.orderPage section {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  grid-gap: 20px;
  gap: 20px;
  max-width: 600px;
}
.orderPage .top-section img {
  width: 100%;
  margin-bottom: 50px;
}

.orderPage .top-section h3 {
  font-family: "Barlow", sans-serif;
  font-size: 1.5rem;
  align-self: flex-start;
  margin-bottom: 30px;
}
.orderPage .top-section p {
  width: 100%;
}
.orderPage .top-section .priceInfo {
  height: 2rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 10px;
}
.orderPage .top-section .priceInfo span {
  font-family: "Barlow", sans-serif;
}
.orderPage .top-section .priceInfo span:nth-child(1) {
  margin-right: 50%;
  font-family: "Barlow", sans-serif;
  font-weight: bold;
  font-size: 1.5rem;
}

.orderPage .top-section .pizzaExplanation {
  font-size: 1.03rem;
  line-height: 1.8rem;
  margin-bottom: 30px;
}

.orderPage section form {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  padding: 30px 0px;
}

.orderPage section form .size {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: space-around;
  grid-gap: 10px;
  gap: 10px;
  height: 50%;
  width: 50%;
}
.orderPage section form .size .label-container {
  display: flex;
  grid-gap: 10px;
  gap: 10px;
}
.orderPage section form .size label {
  display: flex;
  align-items: center;
  grid-gap: 10px;
  gap: 10px;
}

input[type="radio"] {
  width: 40px;
  height: 40px;
  border-radius: 5px;
  border: 0;
  cursor: pointer;
  background-color: #fdc913;
  -webkit-appearance: none;
          appearance: none;
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  -ms-border-radius: 5px;
  -o-border-radius: 5px;
}

input[type="radio"]:checked {
  background-image: url(/static/media/check-solid.f2c7ca56.svg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: 50%;
}

.orderPage section form p {
  height: 50px;
  font-size: 1.3rem;
  font-weight: bold;
}

.orderPage section form .paste {
  display: flex;
  flex-direction: column;
  height: 50%;
  width: 50%;
}
.orderPage section form .paste p {
  height: 50px;
}
.orderPage section form .paste select {
  font-size: 1rem;
  margin-right: 0px;
  border: 2px solid #d9d9d9;
  border-radius: 5px;
  width: 100%;
  height: 50px;
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  -ms-border-radius: 5px;
  -o-border-radius: 5px;
  background-color: #faf7f2;
  font-family: "Barlow";
  padding-left: 10px;
}
.orderPage section form .materials {
  width: 100%;
  margin-top: 50px;
  padding-bottom: 50px;
  display: flex;
  flex-direction: column;
  border-bottom: 1px solid #5f5f5f80;
}
.orderPage section form .materials p:nth-child(1) {
  height: 50px;
  margin: 0px;
}
.orderPage section form .materials p:nth-child(2) {
  height: 2rem;
  font-size: 1rem;
  font-weight: lighter;
  margin: 0;
}

.orderPage section form .materials .additionalMaterials {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  grid-gap: 10px;
  gap: 10px;
  margin: 10px 0px 20px 0px;
  font-size: 1rem;
  height: auto;
}
.orderPage section form .materials .additionalMaterials label {
  width: 180px;
  font-size: 1rem;
  padding: 5px 0px;
  display: flex;
  align-items: center;
  grid-gap: 10px;
  gap: 10px;
}

.orderPage section form .materials p:nth-child(4) {
  margin-top: 50px;
}
.orderPage section form .materials .note {
  font-size: 1rem;
  margin-right: 0px;
  border: 2px solid #d9d9d9;
  border-radius: 5px;
  width: 100%;
  height: 50px;
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  -ms-border-radius: 5px;
  -o-border-radius: 5px;
  background-color: #faf7f2;
}
input::placeholder {
  padding-left: 20px;
}

.orderPage section form .priceCalculation {
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-end;
  width: 100%;
  padding-top: 20px;
}

.orderPage section form .priceCalculation .counter {
  width: 40%;
  display: flex;
  align-items: flex-start;
  justify-content: center;
}
.orderPage section form .priceCalculation button {
  width: 100%;
  height: 50px;
  border: none;
  font-size: 1.5rem;
  background-color: #fdc913;
}

.orderPage section form .priceCalculation .counter button {
  width: 50px;
  height: 50px;
  background: #faf7f2;
}
.orderPage section form .priceCalculation .counter button:nth-child(1) {
  border-top-left-radius: 7px;
  border-bottom-left-radius: 7px;
}
.orderPage section form .priceCalculation .counter button:nth-child(3) {
  border-top-right-radius: 7px;
  border-bottom-right-radius: 7px;
}
.orderPage section form .priceCalculation .counter p {
  width: 50px;
  height: 50px;
  padding: 10px;
  text-align: center;
  background: #faf7f2;
}

.orderPage section form .priceCalculation .summary {
  width: 60%;
  display: flex;
  flex-direction: column;
  align-items: center;
  border: 2px solid #d9d9d9;
  border-top-left-radius: 7px;
  border-top-right-radius: 7px;
  padding: 20px 30px 20px 40px;
  background-color: #faf7f2;
}
.orderPage section form .priceCalculation .summary p {
  font-size: 1.3rem;
  display: flex;
  justify-content: space-between;
  width: 100%;
}
.orderPage section form .priceCalculation .summary p:nth-child(1) {
  color: #292929;
}
.orderPage section form .priceCalculation .summary p:nth-child(2) {
  color: #5f5f5f;
}
.orderPage section form .priceCalculation .summary p:nth-child(3) {
  color: #ce2829;
}

.orderPage section form .priceCalculation .submit-button {
  border-bottom-left-radius: 7px;
  border-bottom-right-radius: 7px;
  width: 60%;
}
.orderPage section form .errorMessage {
  display: block;
  padding: 0px;
  margin: 0px;
  height: 20px;
  font-size: 1rem;
  font-weight: bold;
  color: red;
}

@media (max-width: 600px) {
  .orderPage .top-section {
    padding: 0px 20px;
  }

  .orderPage section form .paste select {
    max-width: 100%;
    overflow: hidden;
  }
  .orderPage section form {
    grid-gap: 30px;
    gap: 30px;
  }
  .orderPage section form .size {
    width: 100%;
    align-items: center;
  }
  .orderPage section form .paste {
    width: 100%;
    align-items: center;
    padding: 20px;
  }
  .orderPage section form .materials {
    align-items: center;
    padding: 20px;
  }
  .orderPage section form .priceCalculation {
    padding: 20px;
  }
}

@media (max-width: 400px) {
  .orderPage section form .priceCalculation {
    grid-gap: 20px;
    gap: 20px;
  }
  .orderPage section form .priceCalculation .counter {
    width: 100%;
  }
  .orderPage section form .priceCalculation .summary {
    width: 100%;
  }
  .orderPage section form .priceCalculation .submit-button {
    width: 100%;
  }
}

input[id="materialList"] {
  width: 45px;
  height: 45px;
  border-radius: 5px;
  border: 0;
  cursor: pointer;
  background-color: #fdc913;
  -webkit-appearance: none;
          appearance: none;
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  -ms-border-radius: 5px;
  -o-border-radius: 5px;
}

input[id="materialList"]:checked {
  background-image: url(/static/media/check-solid.f2c7ca56.svg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: 50%;
}

.main-header {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 20vh;
  width: 100%;
  background-color: #c20608;
  color: white;
}
.main-header .content {
  height: 100%;
  width: 600px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
}

.main-header h1 {
  font-family: "Londrina Solid", sans-serif;
  font-weight: 300;
  font-size: 2.5rem;
  color: white;
}

.main-header nav {
  width: 100%;
  height: 2rem;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  grid-gap: 20px;
  gap: 20px;
}

.main-header nav a {
  text-decoration: none;
  font-size: 1rem;
  color: white;
}

@media (max-width: 600px) {
  nav {
    padding-left: 20px;
  }
}

/* Sarı #FDC913, Açık Gri: #5F5F5F, Koyu Gri #292929, Kırmızı #CE2829, Bej: #FAF7F2 */
